.form_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 5rem;
    margin-bottom: 5rem;
    margin-right: 1rem;

    position: relative;

    max-width: 1300px;
}

.form_wrapper form {
    display: grid;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;

    gap: 10px;

    padding: 1rem 3rem;

    width: 100%;
    box-sizing: border-box;
}

.form_wrapper form .wide {
    grid-column: span 2;
}

.form_wrapper form #password_input_holder {
    grid-column: 1;
}

.form_wrapper .input_holder textarea, .input_holder input[type="date"] {
    font-family: inherit;
}

.form_wrapper .input_holder input, .form_wrapper .input_holder textarea, .form_wrapper .input_holder select {
    margin: 0.5rem;
    padding: 0.5rem;
    border-radius: var(--round_border_width);

    background: rgba(255, 255, 255, 0.4);
    color: white;

    border: rgba(255, 255, 255, 0.3) 0.1rem solid;
    border-right: rgba(255, 255, 255, 0.15) 0.1rem solid;
    border-bottom: rgba(255, 255, 255, 0.15) 0.1rem solid;

    font-size: 1.25em;
    line-height: 2rem;

    display: inline-block;
    width: 22rem;
    /* To make sure the size of select matches all other elements */
    box-sizing: border-box;
}

.form_wrapper .input_holder label {
    font-weight: 500;
    display: inline-block;
}

.form_wrapper label a {
    color: var(--light-blue);
}

.form_wrapper .required::after {
    content: "*";
    color: #bb3838;
}

.form_wrapper .input_holder {
    text-align: right;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.form_wrapper .input_holder input:focus, .form_wrapper .input_holder select:focus, .form_wrapper .input_holder textarea:focus {
    transition: background-color 0.35s ease-in-out;
}

.form_wrapper .input_holder input:focus, .form_wrapper .input_holder select:focus, .form_wrapper .input_holder textarea:focus {
}

.form_wrapper .input_holder label {
    order: -1;
    transition: all 0.2s ease-in-out;
}

.form_wrapper .input_holder.checkbox label, .form_wrapper .input_holder.radio label {
    order: 0;
}

.form_wrapper .input_holder input:focus + label, .form_wrapper .input_holder textarea:focus + label, .form_wrapper .input_holder select:focus + label {
    color: var(--light-blue);
}

.form_wrapper .input_holder select option {
    color: #FFFFFA;
}

.form_wrapper .input_holder input::placeholder, .form_wrapper .input_holder textarea::placeholder {
    color: #FFFFFA;
    font-weight: 700;
    font-size: 1rem;
}

.form_wrapper .input_holder input[type="checkbox"] {
    transform: scale(1.5);
}

.form_wrapper .input_holder input[type="radio"] {
    width: 1rem;
    height: 1rem;
}

/* highlight checked option */
.form_wrapper .input_holder input[type="radio"]:checked + label {
    color: var(--light-blue);
}

.form_wrapper .input_holder .short {
    width: auto;
}

.form_wrapper fieldset {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;

    border: none;

    width: 100%;
    margin: 1rem;
}

.form_wrapper fieldset legend {
    font-weight: 500;
    font-size: 1.4rem;
    width: 100%;
    text-align: center;
}

.form_wrapper fieldset .input_holder {
    justify-content: space-between;
    width: 40%;
}

.form_wrapper fieldset .input_holder input[type="radio"]:checked + label {
    color: var(--light-blue);
}

input[type="tel"]:invalid {
    background: rgba(248, 201, 201, 0.6);
}

@media screen and (min-width: 1321px) {
    .form_wrapper .input_holder input, .form_wrapper .input_holder textarea, .form_wrapper .input_holder select {
        width: 18rem;
    }

    .form_wrapper .input_holder.wide input, .form_wrapper .input_holder.wide textarea, .form_wrapper .input_holder.wide select {
        width: 36rem;
    }

    .form_wrapper .input_holder.checkbox {
        width: 36rem;
        justify-content: flex-start;
    }

    .form_wrapper .input_holder.radio {
        width: 40rem;
        justify-content: flex-start;
    }

    .form_wrapper .input_holder.radio ~ label {
        margin-right: 4rem;
    }

    .form_wrapper .input_holder input[type="checkbox"] {
        width: 2rem;
    }

    .form_wrapper .input_holder .radio_holder {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .form_wrapper .input_holder input[type="radio"] {
        width: 2rem;
    }
}

@media screen and (max-width: 1320px) {

    .form_wrapper .input_holder {
        justify-content: flex-end;
    }

    .form_wrapper form {
        grid-template-columns: 1fr;
    }

    .form_wrapper form .wide {
        grid-column: auto !important;
    }

    .form_wrapper form fieldset {
        width: auto;
    }

    .form_wrapper form fieldset .input_holder {
        width: auto;
    }

    .form_wrapper .input_holder input, .form_wrapper .input_holder textarea, .form_wrapper .input_holder select {
        width: 26rem;
    }

    .form_wrapper .input_holder.checkbox, .form_wrapper .input_holder.radio {
        justify-content: flex-start;
    }

    .form_wrapper .input_holder input[type="checkbox"] {
        width: 2rem;
    }

}

/* Mobile view */
@media screen and (max-width: 850px) {

    main {
        margin: 0;
    }

    .form_wrapper {
        width: 100%;
    }

    .form_wrapper .input_holder {
        flex-direction: column;
        align-items: center;
    }

    .form_wrapper .input_holder label {
        text-align: center;
        width: 100%;
    }

    .form_wrapper .input_holder.checkbox, .form_wrapper .input_holder.radio {
        flex-direction: row;
        justify-content: flex-start;
    }

    .form_wrapper .input_holder.checkbox label, .form_wrapper .input_holder.radio label {
        width: auto;
    }

    .form_wrapper .input_holder input, .form_wrapper .input_holder textarea, .form_wrapper .input_holder select {
        width: 95%;
    }

    .form_wrapper button {
        width: 95%;
    }
}

@media screen and (max-width: 400px) {
    .form_wrapper .input_holder {
        font-size: 1rem;
    }

    input::placeholder, textarea::placeholder {
        font-size: 1rem;
    }

    .form_wrapper .input_holder input, .form_wrapper .input_holder textarea, .form_wrapper .input_holder select {
        font-size: 1rem;
    }

    .form_wrapper .input_holder.checkbox, .form_wrapper .input_holder.radio {
        flex-direction: column;
        justify-content: flex-start;
    }
}